import { useRef } from "react";
import LoudspeakerIcon from "../icons/LoudspeakerIcon";

export const InstructionButton = ({
  instructions,
}: {
  instructions: string;
}) => {
  const instructionUtteranceRef = useRef<SpeechSynthesisUtterance | null>(null);

  const playInstructions = () => {
    if (typeof window !== "undefined" && window.speechSynthesis) {
      window.speechSynthesis.cancel();
      const utterance = new SpeechSynthesisUtterance(instructions);
      instructionUtteranceRef.current = utterance;
      const voices = window.speechSynthesis.getVoices();
      let preferredVoice = voices.find(
        (v) => v.lang === "en-US" && v.name.toLowerCase().includes("female")
      );
      if (!preferredVoice)
        preferredVoice = voices.find((v) => v.lang === "en-US");
      if (!preferredVoice)
        preferredVoice = voices.find((v) => v.lang.startsWith("en"));
      if (preferredVoice) utterance.voice = preferredVoice;
      utterance.pitch = 1;
      utterance.rate = 1;
      utterance.onend = () => {
        instructionUtteranceRef.current = null;
      };
      utterance.onerror = () => {
        instructionUtteranceRef.current = null;
      };
      window.speechSynthesis.speak(utterance);
    }
  };

  return (
    <button
      onClick={playInstructions}
      title="Play Instructions"
      className="py-3 pr-6 pl-3 flex items-center gap-2 shadow-lg/20 text-purple-300 bg-gradient-to-br from-purple-400 to-purple-600 hover:bg-gradient-to-r hover:from-purple-500 hover:to-purple-700 rounded-full focus:outline-none transition-colors"
    >
      <div className="flex items-center justify-center bg-gradient-to-br from-amber-400 to-amber-600 rounded-full p-1 shadow-md/20">
        <LoudspeakerIcon className="w-8 h-8 fill-white drop-shadow-md/20" />
      </div>
      <span className="font-bold text-lg text-white drop-shadow-md/30">
        Instructions
      </span>
    </button>
  );
};
